import React from 'react'
import Color from 'color'
import c from './config'

export const SVGStyles = () => (
  <svg className="gradient-container" style={{position: 'absolute', width: 0, height: 0, top: -1000, right: 1000}}>
    <defs>
      <linearGradient id="eyeIris" gradientTransform="rotate(-45)" spreadMethod="pad">
        <stop offset="5%" stopColor={c.eyes.colorInt} />
        <stop offset="95%" stopColor={Color(c.eyes.colorInt).darken(0.25).hex()} />
      </linearGradient>
      <radialGradient id="glassReflect" gradientTransform={`skewY(-${c.glasses.width}) skewX(${c.glasses.height})`}>
        <stop offset="0%" stopColor="#FFFB"/>
        <stop offset="100%" stopColor="#FFF4"/>
      </radialGradient>
      <linearGradient id="interiorSkin" gradientTransform="rotate(90)" spreadMethod="pad">
        <stop offset="20%" stopColor={Color(c.details.skinColor3).desaturate(0.25).darken(0.1).hex()} />
        <stop offset="80%" stopColor={c.details.skinColor3} />
      </linearGradient>
      <linearGradient id="eyeBow" gradientTransform="rotate(-20)" spreadMethod="pad">
        <stop offset="10%" stopColor={Color(c.details.hairColor).desaturate(0.15).darken(0.15).hex()} />
        <stop offset="100%" stopColor={c.details.hairColor} />
      </linearGradient>
    </defs>
  </svg>
)