import React, { useState, useEffect, useRef, forwardRef } from 'react'
import { Illustration } from 'react-zdog'
import { SVGStyles } from './styles'
import Head from './atoms/head'
import { AppContext, defaultContext } from './context'
import c from './config'

const Container = forwardRef(({ children }, ref) => (
  <div style={{ position: 'relative', width: '100%', paddingTop: '100%' }}>
    <div ref={ref} style={{ position: 'absolute', width: '100%', height: '100%', top: 0, left: 0 }}>
      {children}
    </div>
  </div>
));

/**
 * 3D SCENE
 */
export default function App () {
  /**
   * States
   */
  const [focusCoord, setFocusCoord] = useState(defaultContext.focus)
  const [zoom, setZoom] = useState(1)

  /**
   * Ref
   */
  const illuRef = useRef()

  /**
   * context
   */
  const contextValues = { 
    focus: focusCoord
  }

  /**
   * From mouse move to app focus coordinates
   * @param {object} native event 
   * @returns {void}
   */
  const handleMouseMove = ({ clientX, clientY }) => {
    setFocusCoord({
      x: Math.round(((200 / window.innerWidth) * clientX) - 100) / 100,
      y: Math.round(((200 / window.innerHeight) * clientY) - 100) / 100
    })
  }

  /**
   * Handle illustration zoom
   * @param {object} native event 
   * @returns {void}
   */
  const handleZoom = () => {
    if (!illuRef.current) return

    let bounding = illuRef.current.getBoundingClientRect()
    let viewportSize = Math.min(bounding.width, bounding.height)
    let illuContentSize = c.skull.height + (c.jaw.size / 2)
    let ratio = viewportSize < 500 ? 0.8 : 0.7

    setZoom((viewportSize / illuContentSize) * ratio)
  }

  /**
   * Manage events and init values
   */
  useEffect(() => {
    window.addEventListener('resize', handleZoom)
    window.addEventListener('mousemove', handleMouseMove)
    
    return () => {
      window.removeEventListener('resize', handleZoom)
      window.removeEventListener('mousemove', handleMouseMove)
    }
  }, [])

  /**
   * Init the app
   */
  useEffect(handleZoom, [illuRef])

  /**
   * Render
   */
  return (
    <Container ref={illuRef}>
      <AppContext.Provider value={contextValues}>
        <Illustration 
          zoom={zoom} 
          dragRotate 
          rotate={{ x: 0, y: 0 }}
        >
          <Head /> 
        </Illustration>
        <SVGStyles />
      </AppContext.Provider>
    </Container>
  )
}